.taroify-dialog {
  top: 45%;
  left: 50%;
  width: var(--dialog-width, 640px);
  overflow: hidden;
  font-size: var(--dialog-font-size, var(--font-size-lg, 32px));
  background-color: var(--dialog-background-color, var(--white, #fff));
  border-radius: var(--dialog-border-radius, 32px);
  transition: var(--dialog-transition, var(--animation-duration-base, 0.3s));
  transition-property: transform, opacity;
  backface-visibility: hidden;
}
@media (max-width: 321px) {
  .taroify-dialog {
    width: var(--dialog-small-screen-width, 90%);
  }
}
.taroify-dialog__header {
  padding-top: var(--dialog-header-padding-top, 52px);
  font-weight: var(--dialog-header-font-weight, var(--font-weight-bold, 500));
  line-height: var(--dialog-header-line-height, 48px);
  text-align: center;
}
.taroify-dialog__header--isolated {
  padding: var(--dialog-header-isolated-padding, var(--padding-lg, 48px) 0);
}
.taroify-dialog__content--isolated {
  display: flex;
  align-items: center;
  min-height: 208px;
}
.taroify-dialog__header + .taroify-dialog__content .taroify-dialog__message {
  padding-top: var(--dialog-has-title-message-padding-top, var(--padding-xs, 16px));
  color: var(--dialog-has-title-message-text-color, var(--gray-7, #646566));
}
.taroify-dialog__message {
  flex: 1;
  max-height: var(--dialog-message-max-height, 60vh);
  padding: 52px var(--dialog-message-padding, var(--padding-lg, 48px));
  overflow-y: auto;
  font-size: var(--dialog-message-font-size, var(--font-size-md, 28px));
  line-height: var(--dialog-message-line-height, var(--line-height-md, 40px));
  text-align: center;
  word-wrap: break-word;
  white-space: pre-wrap;
  -webkit-overflow-scrolling: touch;
}
.taroify-dialog__message--left {
  text-align: left;
}
.taroify-dialog__message--right {
  text-align: right;
}
.taroify-dialog__footer {
  display: flex;
  overflow: hidden;
  user-select: none;
}
.taroify-dialog__footer .taroify-dialog__confirm, .taroify-dialog__footer .taroify-dialog__cancel {
  flex: 1;
  height: var(--dialog-button-height, 96px);
  margin: 0;
  border: 0;
}
.taroify-dialog__footer .taroify-dialog__confirm, .taroify-dialog__footer .taroify-dialog__confirm:active {
  color: var(--dialog-confirm-button-text-color, var(--red, #ee0a24));
}
.taroify-dialog__footer--round {
  position: relative;
  height: auto;
  padding: var(--padding-xs, 16px) var(--padding-lg, 48px) var(--padding-md, 32px);
}
.taroify-dialog__footer--round .taroify-dialog__confirm, .taroify-dialog__footer--round .taroify-dialog__cancel {
  height: var(--dialog-round-button-height, 72px);
  font-size: var(--dialog-round-button-font-size, var(--font-size-md, 28px));
  border-radius: unset;
}
.taroify-dialog__footer--round .taroify-dialog__confirm, .taroify-dialog__footer--round .taroify-dialog__confirm:active, .taroify-dialog__footer--round .taroify-dialog__cancel, .taroify-dialog__footer--round .taroify-dialog__cancel:active {
  color: var(--white, #fff);
}
.taroify-dialog__footer--round .taroify-dialog__confirm:first-child, .taroify-dialog__footer--round .taroify-dialog__cancel:first-child {
  border-top-left-radius: var(--border-radius-max, 999px);
  border-bottom-left-radius: var(--border-radius-max, 999px);
}
.taroify-dialog__footer--round .taroify-dialog__confirm:last-child, .taroify-dialog__footer--round .taroify-dialog__cancel:last-child {
  border-top-right-radius: var(--border-radius-max, 999px);
  border-bottom-right-radius: var(--border-radius-max, 999px);
}
.taroify-dialog__footer--round .taroify-dialog__confirm {
  background: var(--dialog-round-confirm-button-background-color, var(--gradient-red, linear-gradient(to right, #ff6034, #ee0a24)));
}
.taroify-dialog__footer--round .taroify-dialog__cancel {
  background: var(--dialog-round-cancel-button-background-color, var(--gradient-orange, linear-gradient(to right, #ffd01e, #ff8917)));
}
.taroify-dialog-bounce-appear, .taroify-dialog-bounce-enter {
  opacity: 0;
  transform: translate3d(-50%, -50%, 0) scale(0.7);
}
.taroify-dialog-bounce-exit-active {
  opacity: 0;
  transform: translate3d(-50%, -50%, 0) scale(0.9);
}